.gallery {
  display: flex;
  flex-wrap: wrap;
  flex-basis: 100%;
  gap: 20px;

  @include tablet() {
    gap: 30px;
  }

  @include desktop() {
    gap: 16px;
  }
}

.card {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  justify-content: center;

  min-width: 280px;
  min-height: 440px;
  @include tablet() {
    flex-basis: calc((100% - 32px) / 2);
  }

  @include desktop() {
    // gap: 16px;

    margin-bottom: 16px;
    flex-basis: calc((100% - 32px) / 3);
  }
}
.card:hover,
.card:focus {
  transform: scale(1.05);
  transition-duration: 1000ms;
  //  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.18);
  cursor: pointer;
}
.card__poster {
  margin: 0;
  box-sizing: border-box;

  width: 100%;
  height: 100%;

  border-radius: 5px;
  @include tablet() {
    height: 455px;
  }

  @include desktop() {
    height: 574px;
  }
}

.card__info {
  justify-content: flex-start;
  flex-grow: 1;
  flex-wrap: wrap;
  margin-top: 10px;
  font-size: 12px;
  line-height: 1.33;
  padding: 0 4px;
  @include tablet() {
    margin-top: 8px;
  }

  @include desktop() {
    margin-top: 12px;
    font-size: 20px;
    line-height: 1.2;
  }
}

.info__title {
  justify-content: flex-start;
  text-transform: uppercase;
  color: var(--primary-text-color);
}

.info__genre,
.info__release-date,
.info__span {
  margin-top: 10px;

  color: var(--third-text-color);
}

.info__vote_average {
  display: inline-flex;
  width: 40px;
  height: 20px;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.17;
  color: #ffffff;
  background: #ff6b01;
  border-radius: 5px;
}
