.dropbtn {
  min-width: 100px;
  height: 30px;
  padding: 0 5px;
  margin-top: 32px;
  // margin-left: 40px;
  border-radius: 50px;

  // background: #111;
  background-color: transparent;
  border: 1px solid  rgba(255, 255, 255, 1);;

  color:rgba(255, 255, 255, 1);;
  text-transform: uppercase;
  font-size: 14px;
  cursor: pointer;
  transition: transform  350ms cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: box-shadow, transform;

  @include tablet {
    min-width: 150px;
    height: 35px;
    font-size: 16px;
  }
  // @include desktop {
  // min-width: 100%;
  // height: 35px;
  // margin-bottom: 10px;
  // margin-left: 40px;
  // font-size: 16px;
  // }
}

.dropbtn:hover,
.dropbtn:focus {
  border: 1px solid  rgba(
  255,
  107,
  8,
  1
);;
  background:  rgba(
  255,
  107,
  8,
  1
);;
  box-shadow: 0px 8px 43px  rgba(
  255,
  107,
  8,
  1
);;
}

.dropdown {
  position: relative;
  display: block;
  text-align: center;
}

.dropdown li:hover {
  background: #ff6b01;
}

.dropdown-content {
  display: none;
  left: 85%;
  transform: translateX(-50%);
  position: absolute;
  top: 70px;
  left: 50%;
  border-radius: 5px;
  background-color: #f1f1f1;
  opacity: 0.9;
  width: 160px;
  height: 150px;
  overflow-y: scroll;
  z-index: 1;
}

.dropdown-content li {
  color: black;
  /* width: 152px;
  height: 44px; */
  padding: 5px 5px;
  font-size: 16px;
  text-decoration: none;
  text-align: center;
  display: block;
  transition: transform 350ms cubic-bezier(0.4, 0, 0.2, 1);
  transition-property: background;
}

.show {
  display: block;
}
