.backdrop__team {
  z-index: 10;
  position: fixed;
  opacity: 1;
  visibility: visible;
  overflow: auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.8);

  // transition: opacity 250ms var(--trans-prop), backround 250ms var(--trans-prop);
}

.modal__team {
  z-index: 5;

  margin-top: 50px;
  margin-bottom: 50px;
  padding: 25px;

  background-color: rgba(245, 244, 250, 1);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.8);
  border-radius: 5px;
}

.team {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden auto;
}

.team__title {
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  color: var(--primary-text-color);
}

.team {
  &__list {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;

    margin-top: calc(-1 * var(--card-set-grid-team));
    margin-left: calc(-1 * var(--card-set-grid-team));
    padding: 2px 2px;
  }

  &__item {
    margin-top: var(--card-set-grid-team);
    margin-left: var(--card-set-grid-team);

    flex-basis: calc(100% / 1 - var(--card-set-grid-team));

    background-color: var(--card-set-grid-bgc);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
  }

  &__box {
    padding: 15px 7px;
    text-align: center;
  }

  &__name {
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    margin-bottom: var(--team-text-mg);
  }

  &__job-title {
    color: var(--footer-text-color);
    font-size: 18px;
    font-weight: 400;
    margin-bottom: var(--team-text-mg);
  }

  &__work {
    color: var(--modal-text-color);
    font-size: 12px;
    font-weight: 400;
    line-height: 1.187;
    min-height: 28.5px;
    margin-bottom: var(--team-text-mg);
  }
}

// 768px
@include tablet() {
  .modal__team {
    width: 768px;
  }

  .team {
    &__list {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }

    &__item {
      flex-basis: calc(100% / 3 - var(--card-set-grid-team));
    }
  }
}

// 1200px
@include desktop() {
  .modal__team {
    width: 1024px;
  }
  .team {
    &__item {
      flex-basis: calc(100% / 4 - var(--card-set-grid-team));
    }
  }
}

.social-list {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 35px;
    height: 35px;

    fill: var(--active-btn-color);

    border-radius: 50%;

    transition: background-color 250ms var(--trans-prop) 0ms,
      fill 250ms var(--trans-prop) 0ms;

    &:hover,
    &:focus {
      background-color: var(--active-btn-color);
      fill: var(--secondary-text-color);
    }
  }

  &__link-footer {
    fill: var(--card-set-grid-bgc);
    background-color: rgba(255, 255, 255, 0.1);

    transition: background-color 250ms var(--trans-prop) 0ms,
      fill 250ms var(--trans-prop) 0ms;

    &:hover,
    &:focus {
      fill: var(--active-btn-color);
      color: red;
    }
  }
}
